.radio label:before {
  content: "";
  border: 1px solid #607d8b;
  border-radius: .5rem;
  flex-shrink: 0;
  width: 1rem;
  min-width: 1rem;
  height: 1rem;
  margin-right: .5rem;
  transition: all .15s;
  display: block;
}

.radio label:after {
  content: "";
  opacity: 0;
  background-color: #1e92e6;
  border-radius: 1rem;
  width: calc(1rem - 6px);
  height: calc(1rem - 6px);
  transition: all .15s;
  display: block;
  position: absolute;
  left: 3px;
}

.radio input:checked + label:after {
  opacity: 1;
}

.input-group {
  border: 1px solid #607d8b80;
  border-radius: .5rem;
  overflow: hidden;
}

.input-group .input {
  background-color: #161721;
  border-radius: 0;
  padding: 0 1rem;
}

