  font-size: 0.875rem;
  background: #22242f;
  border-radius: 0.25rem;
  display: flex;
  padding: 0 0.5rem;
  align-items: center;
  overflow: hidden;
}
.input > label {
  margin-left: 0;
  margin-right: 0;
  display: flex;
  align-items: center;
}
.input > label svg {
  margin-left: 0.25rem;
  width: 0.875rem;
  height: 0.875rem;
}
.input > span {
  font-weight: 600;
  color: #607d8b;
  margin-left: 0.5rem;
}
.input > input {
  -webkit-appearance: none;
  -moz-appearance: textfield;
  appearance: none;
  border: 0;
  background: transparent;
  font-weight: 600;
  margin-left: auto;
  margin-right: 0;
  color: #fff;
  text-align: right;
  flex: 1;
  max-width: 30%;
}
.input > input:focus,
.input > input:active {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  border: 0;
  outline: none;
}
.input input::-webkit-outer-spin-button,
.input input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
.input--flex-input > input {
  max-width: none;
}
.input--modal {
  background-color: #161721;
  padding: 0 1rem;
  border-radius: 0.5rem;
}
.input--modal > span {
  min-width: 1.8125rem;
  text-align: center;
}
.input--with-select {
  overflow: visible;
}
.input--with-select .select {
  color: #607d8b;
  background: none;
  padding: 0;
  font-size: 0.75rem;
  font-weight: 600;
  margin-left: 0.25rem;
  box-shadow: none !important;
}
.input--with-select .select:hover {
  background: transparent;
  box-shadow: none !important;
}
.input--with-select .select--open {
  box-shadow: none !important;
}
.input--with-select .select svg {
  margin-left: 0.375rem !important;
  width: 0.875rem;
  height: 0.875rem;
}
.input--with-select .select ul {
  position: absolute;
  left: auto;
  right: -0.5rem;
  top: calc(100% + 0.25rem);
  min-width: 72px;
}
.input--premium {
  padding-right: 0;
}
.input--premium span {
  margin-right: 0.75rem;
}
.input--premium .premium {
  font-weight: 600;
  margin-left: auto;
  margin-right: 0;
}
.input--premium .buttons {
  width: 2.375rem;
  height: 100%;
  border-left: 2px solid #161721;
}
.input--premium i {
  display: block;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  width: 100%;
  height: 50%;
  border-radius: 0;
  font-size: 0.875rem;
  font-weight: 600;
  line-height: 100%;
  font-style: normal;
  text-align: center;
  cursor: pointer;
  -webkit-user-select: none;
  user-select: none;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}
.input--premium i:hover {
  background: #1c6599;
}
.input--premium i:first-child,
.input--premium i:hover:first-child {
  border-bottom: 1px solid #161721;
}
.input--premium i:last-child,
.input--premium i:hover:last-child {
  border-top: 1px solid #161721;
}
.radio,
.checkbox {
  position: relative;
  margin-bottom: 0.5rem;
  cursor: pointer;
}
.radio input,
.checkbox input {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  width: 0;
  height: 0;
  opacity: 0;
  position: absolute;
  left: 0;
  top: 0;
}
.radio label,
.checkbox label {
  font-size: 0.75rem;
  line-height: 1em;
  display: flex;
  align-items: center;
  position: relative;
}
.radio input:checked + label:before,
.checkbox input:checked + label:before {
  border-color: #1e92e6;
}
.radio.grey label:before,
.checkbox.grey label:before {
  border-color: #607d8b;
}
.radio.grey label:after,
.checkbox.grey label:after {
  background-color: #607d8b;
}
.radio.grey input:checked + label:after,
.checkbox.grey input:checked + label:after {
  background-color: #60fbd0;
}
.radio.teal label:before,
.checkbox.teal label:before {
  border-color: #607d8b;
}
.checkbox {
  position: relative;
  margin-bottom: 0.5rem;
  cursor: pointer;
}
.checkbox label {
  cursor: pointer;
}
.checkbox label:before {
  display: block;
  content: "";
  width: 1.5rem;
  height: 1rem;
  min-width: 1rem;
  border: 1px solid #607d8b;
  border-radius: 0.5rem;
  margin-right: 0.5rem;
  transition: all 0.15s;
  flex-shrink: 0;
}
.checkbox label:after {
  display: block;
  content: "";
  position: absolute;
  left: 3px;
  height: calc(1rem - 6px);
  width: calc(1rem - 6px);
  border-radius: 1rem;
  background: #607d8b;
  transition: all 0.15s;
}
.checkbox label svg {
  margin-left: 0.25rem;
}
.checkbox input:checked + label:after {
  /* background-image: url(); */
  background-repeat: no-repeat;
  background-position: center center;
  left: calc(0.5rem + 3px);
  background-color: #1e92e6;
}
.checkbox input:disabled + label {
  opacity: 0.5;
}
.checkbox a {
  color: #60fbd0;
  display: inline-block;
  margin-left: 0.25rem;
}
.checkbox--red input:checked + label:after {
  background-color: #e53935;
}
.checkbox--red input:checked + label:before {
  border-color: #e53935;
}
.checkbox--teal input:checked + label:after {
  background-color: #60fbd0;
}
.checkbox--teal input:checked + label:before {
  border-color: #60fbd0;
}
.checkbox--white input:checked + label:after {
  background-color: #fff;
}
.checkbox--white input:checked + label:before {
  border-color: #fff;
}
.checkbox--alert input + label:after {
  background-color: #e53935;
}
.checkbox--alert input + label:before {
  border-color: #e53935;
}
.radio label:before {
  display: block;
  content: "";
  width: 1rem;
  height: 1rem;
  min-width: 1rem;
  border: 1px solid #607d8b;
  border-radius: 0.5rem;
  margin-right: 0.5rem;
  transition: all 0.15s;
  flex-shrink: 0;
}
.radio label:after {
  display: block;
  content: "";
  position: absolute;
  left: 3px;
  height: calc(1rem - 6px);
  width: calc(1rem - 6px);
  border-radius: 1rem;
  background-color: #1e92e6;
  transition: all 0.15s;
  opacity: 0;
}
.radio input:checked + label:after {
  opacity: 1;
}
.input-group {
  border: 1px solid rgba(96, 125, 139, 0.5);
  border-radius: 0.5rem;
  overflow: hidden;
}
.input-group .input {
  background-color: #161721;
  padding: 0 1rem;
  border-radius: 0;
