* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Poppins, sans-serif;
}
button {
  font-size: 18px;
  font-weight: 400;
  color: #fff;
  padding: 14px 22px;
  border: none;
  background: #252830;
  border-radius: 6px;
  cursor: pointer;
}
button:hover {
  background-color: #929fc3;
}
button.show-modal,
.modal-box {
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 5px 10px #0000001a;
}
.overlay {
  position: fixed;
  height: 100%;
  width: 100%;
  background: rgba(0, 0, 0, 0.79);
  opacity: 0;
  pointer-events: none;
  z-index: 1000;
}
.overlay.active {
  opacity: 1;
  pointer-events: auto;
  z-index: 999.9;
}
.modal-box {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 380px;
  width: 100%;
  padding: 30px 20px;
  border-radius: 24px;
  background-color: #444c52fa;
  opacity: 0;
  pointer-events: none;
  transition: all 0.3s ease;
  transform: translate(-50%, -50%) scale(1.2);
  z-index: 1000;
}
.modal-box.active {
  opacity: 1;
  pointer-events: auto;
  transform: translate(-50%, -50%) scale(1);
}
.modal-box i {
  font-size: 70px;
  color: #676b78;
}
.modal-box div img {
  margin-top: -10px;
}
.modal-box h2 {
  margin-top: 0;
  font-size: 25px;
  font-weight: 500;
  color: #fff;
}
.modal-box h3 {
  margin-top: 7px;
  font-size: 14px;
  font-weight: 400;
  color: #fff;
  text-align: center;
}
.modal-box .buttons {
  margin-top: 10px;
  margin-bottom: -10px;
}
.modal-box button {
  font-size: 14px;
  padding: 6px 12px;
  margin: 0 10px;
}
.modal-box div {
  align-items: center;
}
